	module.exports = {"data":{"site":{"siteMetadata":{"title":"Stefanus Hinardi","author":"Stefanus Hinardi"}},"markdownRemark":{"id":"/Users/stefanusahinardi/Documents/dev/gatsby-blog/src/pages/about/index.md absPath of file >>> MarkdownRemark","html":"<p>Hi, My name is Stefanus Hinardi</p>\n<p>Currently, I am a graduate student at University of California, Berkeley majoring in Electrical Engineering Computer Science(EECS) focusing in data science and systems.I am working with Mobile Sensing Lab under <a href=\"http://bayen.eecs.berkeley.edu/\">Prof. Alexandre Bayen</a> on traffic assignment problem.</p>\n<p>My interests lie in many things. I like to play music, take photographs and thinker with computers. I do not believe in silos as I feel that the most interesting ideas come from intersections of multiple areas of knowledge. I think that we (homo sapiens) are all in a quest of understanding the world a little better than our ancestors, so why not collaborate?</p>\n<p>I’m happy to chat about any of my technical interests and likely many of yours, music, culture etc. Feel free to get in touch. The best way to reach me is through <a href=\"http://twitter.com/stefanushinardi\">Twitter</a></p>","frontmatter":{"title":"About","date":"March 16, 2018"}}},"pathContext":{"slug":"/about/","previous":{"fields":{"slug":"/quotes/"},"frontmatter":{"title":"Quotes","template":"about"}},"next":{"fields":{"slug":"/perbandingan-akutagawa-lu-xun/"},"frontmatter":{"title":"Bagaimana kritik sosial dipersembahkan di dalam cerita pendek “Catatan Harian Orang Gila” oleh Lu Xun dan “Kappa” oleh Akutagawa Ryunosuke?","template":"blog"}}}}

